@import 'variables';

.form-control {
  background-color: rgba(0,0,0,0.0) !important;
  color: white !important;
  border-radius: 0;
  border: none;
  border-bottom: 1px solid white;
  &:focus {
    box-shadow: none !important;
    transition: border 300ms;
  }
  #spin {
    border: 1px solid white;
    color: white;
    background-color: rgba(0,0,0,0.0);
  }
}
#action-box {
  position: fixed;
  top: 15px;
  right: 15px;
}
body {
  padding: 0;
  margin: 0;
  font-weight: 100;
  font-family: 'Lato', 'Tahoma';
  color: $primaryColor;
  background-color: $backgroundColor;
}

#card-page {
  background-image: url("/images/cards_background.jpg");
}

#note-page {
  background-image: url("/images/notes_background.jpg");
}

.form-control {
  &.title-field {
    margin-top: 25px;
    height: 32pt;
  }
}

#cards {
  h1 {
    color: #FFCE7F;
  }
}

a.inverse-link {
  color: white;
}

html {
  height: 100%;
  width: 100%;
}

h1, h2, h3 {
  margin-bottom: 10px;
}

h1 {
  font-size: 22pt;
  color: #B44012;
}

h2 {
  color: #C26811;
}

h3 {
  color: #e7e7e7;
}
.label-inverse {
  &.label-default {
    background: rgba(0,0,0,0.4);
    border: 1px solid #C26811;
  }
  &.label-success {
    background-color: rgba(92, 184, 92, 0.4);
    border: 1px solid rgba(92, 184, 92, 1);
  }
  &.label-info {
    background-color: rgba(91, 192, 222, 0.4);
    border: 1px solid rgba(91,192,222,1);
  }
}
.background {
  background-image: url("/images/app_background.jpg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: left top;
  background-size: cover;
}
.background-dimmer {
  background-color: rgba(0,0,0,0.3);
}
.background, .background-dimmer {
  display: block;
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.container {
  background-color: rgba(0,0,0,0.6);
  padding-bottom: 35px;
  margin-top: 35px;
}
.index-page .container {
  margin-top: 0px;
}

.navbar {
  .container {
    background: none;
    padding-bottom: 0px;
    margin-top: 0px;
  }
}
.navbar-inverse {
  background-color: rgba(0,0,0, 0.8);
}
.index-page {
  .content {
    margin-top: 25px;
    padding-bottom: 25px;
  } 
}


.main {
  width: 75%;
  display: inline-block;
}
.page {
  padding: 0 5px;
}
.right-aside {
  padding-top: 1em;
  padding-left: 5px;
  padding-right: 5px;
  width: 20%;
  margin-left: 2%;
  display: inline-block;
  vertical-align: top;
}

.left-options {
  position: fixed;
  top: 25px;
  left: 25px;
  * {
    margin: 5px 0;
    display: block;
  }
}

.list-unstyled {
  list-style-type: none;
  margin: 0;
  li {
    margin-bottom: 2px;
  }
}

.side-note {
  display: inline-block;
}
.commentList {
  max-height: 200px;
  overflow-y: scroll;
}
