//     text-align: center;
//     display: inline-block;
// }

.title {
    font-size: 96px;
    font-weight: 100;
}

// cke styles
// 
.cke_chrome {
  border: none !important;
}
.cke_bottom, .cke_top {
  background: rgba(0,0,0,0.0) !important;
}

.cke_contents {
  background-color: none !important;
  height: 450px !important;
}
.cke_maximized {
  .cke_contents {
    height: inherit;
  }
}
#cards {
  .cke_contents {
    height: 200px !important;
  }
}

.cke_top {
  color: #363636;
}
.cke_toolgroup, input[type="text"] {
  margin: 0 6px 3px 0;
  padding: 5px;
}

.form-control {
  &.transparent {
    color: #e6e6e6;
    font-size: 22pt;
    border: none;
    background: rgba(0,0,0,0.0);
    border-radius: 0;
    border-bottom: 1px solid #ddd;
    option {
      color: white;
      font-size: 14pt;
      background-color: rgba(0,0,0,0.8);
    }
  }
}
select {
  border: none;
}

input[type="text"]:focused {
  border: none;
  border-bottom: 1px solid #ddd;
} 

a.btn-secondary, a.btn-primary, a.btn-success {
  color: white;
}

.btn-danger {
  background-color:  rgba(217, 83, 79, 0.2) !important;
}


.index-page {
  .title {
    font-size: 13pt;
    font-weight: 600;
  }
  // .row {
  //   padding: 5px 10px;
  //   &:nth-child(even) {
  //     background-color: rgba(0, 0, 0, 0.3);
  //   }
  // }
  .actions {
    float: right;
    .btn-secondary {
      padding: 2px 5px;
    }
  }
}
.btn {
  text-decoration: none;
}


.cke_maximized {
  &.cke_inner {
    background-color: rgba(0,0,0,0.8);
  }
}
